import { Injectable } from "@angular/core";
import { Moeda } from "../Models";

@Injectable({
  providedIn: "root"
})
export class MoedaService {
  private moedas: Moeda[];

  constructor() {}

  private moedasObj = [
    //http://fixer.io
    { sigla: "AUD", descricao: "Dlar australiano" },
    { sigla: "BGN", descricao: "Lev blgaro" },
    { sigla: "BRL", descricao: "Real brasileiro" },
    { sigla: "CAD", descricao: "Dlar canadense" },
    { sigla: "CHF", descricao: "Franco suo" },
    { sigla: "CNY", descricao: "Yuan Chins" },
    { sigla: "CZK", descricao: "Coroa Repblica Tcheca" },
    { sigla: "DKK", descricao: "Coroa dinamarquesa" },
    { sigla: "EUR", descricao: "Euro" },
    { sigla: "GBP", descricao: "Libra Esterlina" },
    { sigla: "HKD", descricao: "Dlar de Hong Kong" },
    { sigla: "HRK", descricao: "Coroa Crocia" },
    { sigla: "HUF", descricao: "Florim hngaro" },
    { sigla: "IDR", descricao: "Rupia indonsia" },
    { sigla: "ILS", descricao: "Novo shekel israelense" },
    { sigla: "INR", descricao: "Rupia indiana" },
    { sigla: "JPY", descricao: "Iene japons" },
    { sigla: "KRW", descricao: "Won sul-coreano" },
    { sigla: "MXN", descricao: "Peso mexicano" },
    { sigla: "MYR", descricao: "Malsia Ringgit" },
    { sigla: "NOK", descricao: "Coroa Noruega" },
    { sigla: "NZD", descricao: "Dlar da Nova Zelndia" },
    { sigla: "PHP", descricao: "Peso filipino" },
    { sigla: "PLN", descricao: "Zoty Polnia" },
    { sigla: "RON", descricao: "Leu romeno" },
    { sigla: "RUB", descricao: "Belarus Ruble" },
    { sigla: "SEK", descricao: "Coroa Sucia" },
    { sigla: "SGD", descricao: "Dlar de Singapura" },
    { sigla: "THB", descricao: "Baht Tailndia" },
    { sigla: "TRY", descricao: "Lira turca" },
    { sigla: "USD", descricao: "Dlar dos Estados Unidos" },
    { sigla: "ZAR", descricao: "Rand frica do Sul" }
  ];

  listarTodas(): Moeda[] {
    if (this.moedas) {
      return this.moedas;
    }

    this.moedas = [];

    for (const moedaObj of this.moedasObj) {
      const moeda: Moeda = new Moeda();
      Object.assign(moeda, moedaObj);
      this.moedas.push(moeda);
    }

    return this.moedas;
  }
}
